import d3 from 'd3'
import _ from 'lodash'
import { isNoData } from '../util/util'

export default class Pie {
  /**
   * 饼图默认配置项
   * @return {object} 默认配置项
   */
  defaultSetting () {
    return{
      width: 360,
      height: 190,
      dur: 750, // 动画过度时间
      itemStyle: {
        margin: {
          top: 25,
          left: 0,
          bottom: 60,
          right: 0
        },
        colors: ['#e52408', '#f45a05', '#f4ad05', '#fdea0a', '#e61149'],
        innerRadius: 70,
        outerRadius: 50
      },
      bgItemStyle: {
        stroke: '#3d53a4',
        fill: '#0f1738',
        strokeWidth: 1
      }
    }  
  }

  constructor(selector, opt) {
    // 获取配置项
    const defaultSetting = this.defaultSetting()
    this.config = _.merge({}, defaultSetting, opt)
    const { width, height, itemStyle } = this.config  
    const { innerRadius, outerRadius, margin } = itemStyle
    const { top, bottom } = margin
    // 创建svg元素
    const svg = d3.select(selector)
      .append('svg')
      .attr('width', width + top)
      .attr('height', height + bottom)

    // 背景path组元素  
    this.bgArcGroup = svg.append('g')  
      .classed('bg-arc-group', true) 
      .call(this.gTrans.bind(this))

    // path组元素   
    this.arcGroup = svg.append('g') 
      .classed('arc-group', true) 
      .call(this.gTrans.bind(this))
    // 文字组元素
    this.textGroup = svg.append('g')
      .classed('text-group', true) 
      .call(this.gTrans.bind(this))
    // 线条组元素
    this.lineGroup = svg.append('g')
      .classed('line-group', true) 
      .call(this.gTrans.bind(this))

    // 转换原始数据为能用于绘图的数据
    this.pie = d3.layout.pie()
      .sort(null)
      .value(function(d) {
        return d.value
      })
    this.pieData = []
    
    // const iRadius = innerRadius - top - bottom
    // const oRadius = outerRadius - top - bottom

    this.svg = svg

    // 创建弧生成器(计算弧形路径的函数)
    this.arc = d3.svg.arc()
      .innerRadius(innerRadius)
      .outerRadius(outerRadius) 
    // 背景弧  
    this.bgArc = d3.svg.arc()
      .innerRadius(innerRadius)   
      .outerRadius(outerRadius + 35)   
    // hover事件用的  
    this.arcHover = d3.svg.arc()
      .innerRadius(15 )   
      .outerRadius(30 )   
    // 线条的
    this.lineArc = d3.svg.arc()
      .innerRadius(1.1 * innerRadius)
      .outerRadius(1.1 * outerRadius)
    // 文字  
    this.lEndArc = d3.svg.arc()
      .innerRadius(1.6 * innerRadius)
      .outerRadius(1.6 * outerRadius)
  }

  render(data) {
    const self = this
    const { width, height } = self.config
    // 判断数据是否为空
    if(!data || !data.length) {
      isNoData(self.svg, { width, height })
      return false
    }
    self.pieData = self.pie(data)  
    // 渲染背景弧  
    self.renderArcGroup(self.bgArcGroup, 1) 
    // 数据弧
    self.renderArcGroup(self.arcGroup, 2) 
    // 文字
    self.renderTextGroup(self.textGroup)
    // 线条 
    self.renderlineGroup(self.lineGroup)
  }

  /**
   *  渲染数据(path)
   *  @example: [example]
   *  @param    {[type]}  g [description]
   *  @return   {[type]}  [description]
   */
  renderArcGroup(g, type) {
    const self = this
    // 获取update部分 
    let setArc = null
    let arc = null
    if(type === 1){
      setArc = self.setBgArcPathAttribute
      arc = this.bgArc
    } else{
      setArc = self.setArcPathAttribute  
      arc = self.arc 
    }
      
    let update = g.selectAll('path')
      .data(self.pieData)
      .call(setArc.bind(self), arc)

    // 处理enter部分  
    update.enter()
      .append('path')
      .call(setArc.bind(self), arc)

    // 处理exit部分
    update.exit().remove() 
  }

  /**
   *  渲染文字(path)
   *  @example: [example]
   *  @param    {[type]}  g [description]
   *  @return   {[type]}  [description]
   */
  renderTextGroup(g) {
    const self = this
    // 获取name update部分 
    let updateN = g.selectAll('.name-text')
      .data(self.pieData)
      .call(self.setTextAttribute.bind(self), 1)
    // 处理enter部分  
    updateN.enter()
      .append('text')
      .call(self.setTextAttribute.bind(self), 1)
    // 处理exit部分
    updateN.exit().remove()      

    // 获取value update部分 
    let updateV = g.selectAll('.value-text')
      .data(self.pieData)
      .call(self.setTextAttribute.bind(self), 2)
    // 处理enter部分  
    updateV.enter()
      .append('text')
      .call(self.setTextAttribute.bind(self), 2)
    // 处理exit部分
    updateV.exit().remove()
  }

  /**
   *  设置文字属性
   *  @param    {object}  text text元素
   *  @param    {number}  type 类型(name,value)
   *  @return   {object}  null
   */
  setTextAttribute(text, type) {
    const self = this
    const { outerRadius } = self.config.itemStyle
    text.attr('fill', (d, i) => `${type === 1 ? '#fff' : self.getColor(i)}`)
      .attr('text-anchor', (d) => self.midAngel(d) < Math.PI ? 'start' : 'end')
      .attr('font-size', 14)
      .attr('class', `${type === 1 ? 'name-text' : 'value-text'}`)
      .attr('transform', (d) => {
        let pos = self.lEndArc.centroid(d)
        let radius = outerRadius + 40
        pos[0] = radius * (self.midAngel(d) < Math.PI ? 1.1 : -1.1)  
        return `translate(${pos})`
      })
      .attr('dy', `${type === 1 ? '1.2em' : '-0.5em'}`)
      .text((d) => `${type === 1 ? d.data.name : d.data.value}`)
  }
 
  /**
   *  线条组数据渲染
   *  @param    {object}  g g元素
   *  @return   {object}  null
   */
  renderlineGroup(g) {
    const self = this
    // 获取update部分 
    let update = g.selectAll('polyline')
      .data(self.pieData)
      .call(self.setPolylineAttribute.bind(self))
    // 处理enter部分  
    update.enter()
      .append('polyline')
      .call(self.setPolylineAttribute.bind(self))
    // 处理exit部分
    update.exit().remove()    
  }

  /**
   *  线条属性设置
   *  @param    {object}  polyline polyline元素
   *  @return   {object}  null
   */
  setPolylineAttribute(polyline) {
    const self = this
    const { outerRadius } = self.config.itemStyle
    polyline.attr('fill', 'none')
      .attr('stroke', (d, i) => self.getColor(i))
      .attr('stroke-width', 1)
      .attr('points', (d) => {
        let pos = self.lEndArc.centroid(d)
        let radius = outerRadius + 80
        pos[0] = radius * (self.midAngel(d) < Math.PI ? 1.1 : -1.1)  
        return [self.lineArc.centroid(d), self.lEndArc.centroid(d), pos] 
      })
  }

  /**
   *  path属性设置
   *  @param    {object}  path path元素
   *  @param    {function}  arc  arc方法
   *  @return   {object}  null
   */
  setArcPathAttribute(path, arc) {
    const self = this
    path.attr('d', (d) => arc(d))
      .attr('fill', (d, i) => self.getColor(i))
      .attr('stroke', '#040d1a')
      .attr('stroke-opacity', 0.5)
      .attr('stroke-width', 1)
  }

  /**
   *  背景path属性设置
   *  @param    {object}  path path元素
   *  @param    {function}  arc  arc方法
   *  @return   {object}  null
   */
  setBgArcPathAttribute(path, arc) {
    const self = this
    const { fill, stroke, strokeWidth } = self.config.bgItemStyle
    path.attr('d', (d) => arc(d))
      .attr('fill', fill)
      .attr('stroke', stroke)
      .attr('stroke-width', strokeWidth)
  }
 
  /**
   *  g元素tansfrom的位置 
   *  @param    {g}  g g元素
   *  @return   {object}  null
   */
  gTrans(g) {
    const self = this
    const { width, height, itemStyle } = self.config
    const { top } = itemStyle.margin
    g.attr('transform', `translate(${width / 2}, ${height / 2 + top })`)
  }

  /**
   *  计算弧长的中心位置  
   *  @param    {[type]}  d [description]
   *  @return   {object}  null
   */
  midAngel(d) {
    // 计算弧长的中心位置 =（起始弧度 + 终止弧度）/2 = 弧度的中心位置
    return d.startAngle + (d.endAngle - d.startAngle) / 2
  }

  /**
   *  获取饼图填充色
   *  @example: [example]
   *  @param    {numbter}  idx [下标]
   *  @return   {object}  null
   */
  getColor(idx) {
    const self = this
    const { colors } = self.config.itemStyle
    // 默认颜色
    let defauleColor = [
      '#38f3ff', '#da2c59', '#5ab1ef', '#ffb980', '#d87a80',
      '#8d98b3', '#e5cf0d', '#97b552', '#95706d', '#dc69aa',
      '#07a2a4', '#9a7fd1', '#588dd5', '#f5994e', '#c05050',
      '#59678c', '#c9ab00', '#7eb00a', '#6f5553', '#c14089'
    ]
    let palette = _.merge([], defauleColor, colors)
    return palette[idx % palette.length]  
  }  
}



