import $ from 'jquery'
import countHbs from '../templates/count.hbs'
import {fetch} from '@/util/request'

export default class renderCount {
  /**
   * 渲染图表组件
   *
   * @param {array} data
   * @memberof RenderData
   */
  render() {
    fetch('fetchCount', (data) => {
      $('.fkCount').html(countHbs(data.fkCount))
      // 维稳态势
      $('.wwCount').html(countHbs(data.wwCount))
    })
  }

  init() {
    this.render()
  }
}



