/**
 * @Author:      zhanghq
 * @DateTime:    2017-10-13 14:24:06
 * @Description: 中国地图
 * @Last Modified By:   zhanghq
 * @Last Modified Time:    2017-10-13 14:24:06
 */
import * as d3 from 'd3'
import _ from 'lodash'
import {genSVGDocID, isNoData} from '../util/util'
import {getCenters, getZoomScale} from './util'
import fiterHbs from './hbs/filter.hbs'
import {addLegend} from './legend'
import mapBg from './images/china-map.png'
// import light from './images/ligth.png'
import mapJson from './mapJson/china.json'

const filterId = genSVGDocID()
let centerP = []
let setTime = 30000

export default class ChinaMap {
  /**
   * 地图默认配置项
   * @return {object} 默认配置项
   */
  defaultSetting () {
    return{
      width: 1022,
      height: 800,
      dur: 10,
      itemStyle: {
        fill: ['#01fbfe', '#ffc702'],
        minR: 4, // 最小半径
        maxR: 20, // 最大半径
        strokeWidth: 15, 
        strokeOpacity: 0.5,
        exponent: 0.6 // 比例尺指数
      },
      // 图例配置项
      legend: {
        fill: ['#01fbfe', '#ffc702'],
        width: 45,
        height: 18,
        bottom: 60,
        data: ['来津人员', '离津人员'],
        text: {
          fontSize: 14,
          margin: {
            top: 5,
            left: 20
          }
        }
      }
    }  
  }

  /**
   * Creates an instance of ChinaMap
   * @param {string} selector 容器元素选择器
   * @param {object} opt 图表组件配置项
   */
  constructor(selector, opt) {
    // 获取配置项
    const defaultSetting = this.defaultSetting()
    this.config = _.merge({}, defaultSetting, opt)
    const { width, height } = this.config  
    // 创建svg元素
    const svg = d3.select(selector)
      .append('svg')
      .attr('width', width)
      .attr('height', height)
      .style('background', `url(${mapBg}) no-repeat`)
      .style('background-size', '100% 99%')

    const defs = svg.append('defs')

    defs.html(fiterHbs({
      filterId: filterId 
    }))  
      
    // 地图路径组元素  
    this.mapPathGroup = svg.append('g')
      .attr('class', 'map-path-group')  
    // 进入组元素  
    const enterGroups = svg.append('g')
      .attr('class', 'enter-groups')  
    // 出去组元素  
    const outGroups = svg.append('g')
      .attr('class', 'out-groups')   

    // 迁入数据线条组元素  
    this.enterLineGroup = enterGroups.append('g')  
      .attr('class', 'enter-line-group')  
    // 迁出数据线条组元素  
    this.outLineGroup = outGroups.append('g')  
      .attr('class', 'out-line-group')

    // 迁入数据组元素  
    this.enterDataGroup = enterGroups.append('g')  
      .attr('class', 'enter-data-group')  
    // 迁出数据组元素  
    this.outDataGroup = outGroups.append('g')  
      .attr('class', 'out-data-group') 

    this.enterLightGroup = enterGroups.append('g')  
      .attr('class', 'enter-light-group') 

    this.outLightGroup = outGroups.append('g')  
      .attr('class', 'out-light-group')  

    this.svg = svg  

    const features = mapJson.features
    // 获取地图缩放值
    const scale = getZoomScale(features, width, height)
    // 获取地图显示的中心点
    const center = getCenters(features)
    // 定义一个投影函数
    const projection = d3.geo.mercator()  
      .scale(scale * 55)
      .center(center)
      .translate([width / 2, height / 2])

    // 定义一个路径函数  
    const pathMethod = d3.geo.path()  
      .projection(projection) 

    // 渲染地图path  
    this.renderMapPath(pathMethod)  
    
    // 定时器   
    this.mapTimer = null  
    this.setInterval(0)  
    addLegend(this)
    setTimeout(() => {
      this.renderAreaName()
    }, 1000)
  }

  /**
   *  渲染
   *  @example: [example]
   *  @param    {[type]}  data [description]
   *  @return   {[type]}  [description]
   */
  render(data) {
    const self = this
    const { width, height } = self.config
    // 判断数据是否为空
    if(!data) {
      isNoData(self.svg, { width, height })
      return false
    }
    
    // 渲染地图数据
    self.renderMapData(data)
  }

  /**
   *  渲染地图路径
   *  @example: [example]
   *  @param    {[type]}  pathMethod [description]
   *  @param    {[type]}  features   [description]
   *  @return   {[type]}  [description]
   */
  renderMapPath(pathMethod) {
    const self = this
    // 获取update部分并处理  
    let update = self.mapPathGroup.selectAll('path')
      .data(mapJson.features)
      .call(this.setPathAttribute, pathMethod)
    // 获取并处理enter部分  
    update.enter()
      .append('path')  
      .call(this.setPathAttribute, pathMethod)
    // 获取并处理exit部分  
    update.exit().remove()  
  }

  /**
   *  设置地图路径属性
   *  @param    {object}  path       path元素
   *  @param    {function}  pathMethod path方法
   */
  setPathAttribute(path, pathMethod) {
    path.attr('stroke-width', 0)
      .attr('fill', '#050840')
      .attr('opacity', 0)
      .attr('d', pathMethod)
      .attr('class', (d, i) => {
        let center = pathMethod.centroid(d)
        center[1] = center[1] + 30
        // 以下几个点有点跑偏(地图与图片不能完全吻合)，移动下位置
        const NAMES = ['天津市', '海南省', '新疆维吾尔自治区', '台湾省', '吉林省', '辽宁省', '北京市']
        let name = d.properties.name
        let index = NAMES.indexOf(name)
        switch(index) {
          case 0:
            // // 保留中间点天津市
            centerP = pathMethod.centroid(d)
            break
          case 1:
            center[0] = center[0] + 10
            break
          case 2:
            center[0] = center[0] - 40
            break  
          case 3:
            center[0] = center[0] + 30    
            break
          case 4:
          case 5:
            center[0] = center[0] - 20
            center[1] = center[1] - 30 
            break 
          case 6:
             center[0] = center[0] - 20
             center[1] = center[1] - 10 
             break
          default:
            break  
        }
        d.center = center
        return `path-${i}`
      })
  }

  /**
   *  渲染地图数据(后端返回的值)
   *  @param    {array}   data  地图数据
   *  @return   {object}  null
   */
  renderMapData(data) {
    const self = this
    // 迁入数据
    let enterGroup = self.enterDataGroup
    let enterLine = self.enterLineGroup
    let enterData = data.inTianjin
    const type = [2, 1]
    self.renderMarker(enterGroup, enterData, type[0]) 
    self.renderLine(enterLine, enterData, type[0]) 

    // 迁出数据
    let outGroup = self.outDataGroup
    let outLine = self.outLineGroup
    let outData = data.outTianjin
    self.renderMarker(outGroup, outData, type[1])
    self.renderLine(outLine, outData, type[1]) 
    // i决定了画几个动画小圆
    for(let i = 0; i < 5; i++) {
      self.renderLight(self.enterLightGroup, enterData, type[0], i)
      self.renderLight(self.outLightGroup, outData, type[1], i)
    }

  }

  /**
   *  渲染线条 path路径
   *  @param    {object}  group 线条g元素
   *  @param    {array}   data  地图数据
   *  @param    {number}  type  类型(来津、离津)
   *  @return   {object}  null
   */
  renderLine(group, data, type) {
    const self = this
    // 添加圆点组元素
    let update = group.selectAll('path')
      .data(data)
      .call(self.setLinePathAttribute.bind(self), type)
    // 处理enter部分  
    update.enter().append('path')
      .call(self.setLinePathAttribute.bind(self), type) 
    // 获取并处理exit部分  
    update.exit().remove()
  }

  /**
   * 画线
   * @param  {array} roots 根元素
   * @param  {array} posi  坐标点
   * @param  {object} cfg  配置项
   *  @return   {object}  null
   */
  setLinePathAttribute(path, type) {
    const self = this
    const { fill } = self.config.itemStyle
    path.attr('class', `${type === 1 ? 'out-line' : 'enter-line'}`)
      .attr('stroke-width', 1)
      .attr('fill', 'none')
      .attr('stroke', `${type === 1 ? fill[0] : fill[1]}`)
      .attr('d', (d)=> {
        let posi = self.getPathCenter(d)
        let QPosi = [centerP[0] + (posi[0] - centerP[0]) / 1.3, centerP[1]]
        let dPath = ''
        if(type === 2){
          dPath = `M ${posi[0]} ${posi[1]} Q${QPosi[0]} ${QPosi[1]} ${centerP[0]} ${centerP[1]}`
        }else {
          dPath = `M ${centerP[0]} ${centerP[1]} Q${QPosi[0]} ${QPosi[1]} ${posi[0]} ${posi[1]}`
        }
        return dPath
      })
      .attr('id', (d, i) => `mpath${i}${type}`)
  }

  /**
   *  渲染数据圆点
   *  @param    {object}  group  圆点g元素
   *  @param    {array}   data  地图数据
   *  @param    {number}  type  类型(来津、离津)
   *  @return   {object}  null
   */
  renderMarker(group, data, type) {
    const self = this
    // 
    const { minR, maxR, exponent } = self.config.itemStyle
    let dataset = []
    data.forEach(function(d) {
      dataset.push(d.value)
    })
    // 指数比例尺
    let scale = d3.scale.pow()
      .domain([0, d3.max(dataset)])
      .range([minR, maxR])
      .exponent(exponent) // 设置指数  

    // 添加圆点组元素
    let update = group.selectAll('.circle')
      .data(data)
      .call(self.setCircleAttribute.bind(self), scale, type)
    // 获取并处理enter部分    
    update.enter().append('circle')
      .call(self.setCircleAttribute.bind(self), scale, type) 
    // 获取并处理exit部分  
    update.exit().remove()

    // 添加value
    let updateT = group.selectAll('.text')
      .data(data)
      .call(self.setDataTextAttribute.bind(self), scale, type)
    // 获取并处理enter部分    
    updateT.enter().append('text')
      .call(self.setDataTextAttribute.bind(self), scale, type) 
    // 获取并处理exit部分  
    updateT.exit().remove()
  }

  /**
    *  渲染动画(小圆点)
    *  @param    {object}  group 动画g元素
    *  @param    {array}   data  地图数据
    *  @param    {number}  type  类型(来津、离津)
    *  @param    {number}  index 用于动画延迟时间
    *  @return   {object}  null
  */
  renderLight(group, data, type, index) {
    const self = this
    // 添加圆点组元素
    let update = group.selectAll(`.light-circle-${index}`)
      .data(data)
      .call(self.setLightAttribute.bind(self), type, index)

    // 获取并处理enter部分    
    update.enter().append('circle')
      .call(self.setLightAttribute.bind(self), type, index) 
    // 获取并处理exit部分  
    update.exit().remove()
  }

  /**
   *  设置动画小圆点属性
   *  @param    {object}  circle circle元素
   *  @param    {number}  type  类型(来津、离津)
   *  @param    {number}  index 用于动画延迟时间
   *  @return   {object}  null
   */
  setLightAttribute(circle, type, index) {
    const self = this 
    circle.attr('class', `light-circle light-circle-${index}`)
      .attr('fill', `${type === 2 ? '#fbff98' : '#aff4ff'}`)
      .attr('filter', `url(#${filterId})`)
      .attr('r', 2.5)
      .call(self.addAnimateMotion.bind(self), type, index)  
  } 
 
  /**
   *  创建动画
   *  @param    {object}  circle circle元素
   *  @param    {number}  type  类型(来津、离津)
   *  @param    {number}  index 用于动画延迟时间
   *  @return   {object}  null
   */
  addAnimateMotion(circle, type, index) {
    const self = this
    const { dur } = self.config
    if(!circle.select('animateMotion').node()){
      circle.append('animateMotion')
        .attr('begin', index)
        .attr('dur', dur)
        .attr('repeatCount', 'indefinite')
        .append('mpath')
        .attr('xlink:href', (d, i) => `#mpath${i}${type}`)
    }
  }

  /**
   *  设置数据的value属性
   *  @param    {object}  text text元素
   *  @return   {object}  null
   */
  setDataTextAttribute(text) {
    const self = this
    text.attr('class', 'text')
      .attr('fill', '#fff')
      .attr('x', (d) => {
        let cx = self.getPathCenter(d)
        return cx[0]
      })
      .attr('y', (d) => {
        let cy = self.getPathCenter(d)
        return cy[1] + 20
      })
      .text((d) => d.value)
  }

  /**
   *  设置撒点圆的属性
   *  @param    {object}  circle circle元素
   *  @param    {function}  scale  scale函数
   *  @param    {number}  type  类型(来津、离津)
   *  @return   {object}  null
   */
  setCircleAttribute(circle, scale, type) {
    const self = this
    const { fill, strokeWidth, strokeOpacity } = self.config.itemStyle
    circle.attr('fill', `${type === 1 ? fill[0] : fill[1]}`)
      .attr('stroke-width', strokeWidth)
      .attr('stroke-opacity', strokeOpacity)
      .attr('stroke', `${type === 1 ? fill[0] : fill[1]}`)
      .attr('class', () => `circle outerCircle${type}`)
      .attr('cx', (d) => {
        let cx = self.getPathCenter(d)
        return cx[0]
      })
      .attr('cy', (d) => {
        let cy = self.getPathCenter(d)
        return cy[1]
      })
      .attr('r', (d) => scale(d.value))
  }

  /**
   * 获取path中心位置
   * @param  {string} d        区域名字
   * @return {array}           中心点
   */
  getPathCenter(d) {
    let features = mapJson.features
    for(let j = 0, len = features.length; j < len; j++) {
      if(d.name === features[j].properties.name){
        return features[j].center
      }
    } 
  }

  /**
   *  渲染区域名字
   *  @return   {object}  null
   */
  renderAreaName() {
    const self = this
    let features = mapJson.features

    let g = self.svg.append('g')
      .attr('class', 'area-name')

    g.selectAll('text')
      .data(features)
      .enter()
      .append('text')
      .attr('fill', '#fff')
      .text((d) => {
        return d.properties.name
      })  
      .attr('x', (d) => {
        return d.center[0] 
      })
      .attr('y', (d) => {
        return d.center[1]
      })
  }

  /*
    定时切换迁入迁出数据
   */
  setInterval(n) {
    let t = n
    const self = this
    clearInterval(self.mapTimer)
    self.mapTimer = setInterval(function() {
      t++ 
      self.switchData( t % 2 )
    }, setTime)
  }

  /**
   *  切换数据
   *  @param    {number}  i (0, 1)
   *  @return   {object}  null
   */
  switchData(i) {
    if(i === 0) {
      d3.select('.enter-groups').style('opacity', 1)
      d3.select('.out-groups').style('opacity', 0)
    } else{
      d3.select('.out-groups').style('opacity', 1)
      d3.select('.enter-groups').style('opacity', 0)
    }
  }

}



