export default {
  url: '/highrisk/tops',
  mock: {
    'code': 1,
    'msg': 'success',
    'result': {
      'inTianjin|5': [
        {
          'name': '@city', // 随机生成一个（中国）市
          'value|1-1000': 1
        }
      ],
      'outTianjin|5': [
        {
          'name': '@city', // 随机生成一个（中国）市
          'value|1-1000': 1
        }
      ]
    }
  }
}



