import $ from 'jquery'
import hbs from '../templates/weiwenArea.hbs'
import {fetch} from '@/util/request'

export default class renderCount {
    /**
     * 渲染图表组件
     *
     * @param {array} data
     * @memberof RenderData
     */
    render() {
        let self = this
        fetch('fetchWeiwenArea', (data) => {
            let max = 0
            $.each(data.weiwenArea, (i, e) => {
                if (e.value2 > max) {
                    max = e.value2
                }
            })
            $.each(data.weiwenArea, (i, e) => {
                e.percent2 = self.calc(e.value2, max)
                e.percent1 = self.calc(e.value1, e.value2) * e.percent2 / 100
            })
            $('.area').html(hbs(data.weiwenArea))

            $('.area ul li:last-child')
                .each((i, e) => {
                    // $(e).animate({width: $(e).data('width')}, 1000, 'swing') //启用动画
                    $(e).css({width: $(e).data('width')})
                })

            $('#dq').liMarquee({
                direction: 'up',
                scrollamount: 40
            })
        })
    }

    /**
     * 计算百分比
     * @param {number} value1 被除数
     * @param {number} value2 除数
     * @returns {string} 百分比
     */
    calc(value1, value2) {
        return (value1 / value2 * 100).toFixed(2)
    }
}



