/**
 * @Author:  xieyang
 * @DateTime:    2017-01-17 16:33:38
 * @Description: 左边高危人数(两个饼图渲染)
 * @Last Modified By:   xieyang
 * @Last Modified Time:
 */

import $ from 'jquery'
import hbs from '../templates/populationMovement.hbs'
import {fetch} from '@/util/request'
import { Pie } from '@/charts/index'

export default class renderPopulationMovement {

  constructor() {
    $('.anti-terrorism').append(hbs)
    // 进入
    this.inPie = new Pie('.come .at-theday-chart')
    const outPieConfig = {
      itemStyle: {
        colors: ['#1a9dff', '#0eebff', '#46faff', '#811ef8', '#504afd']
      }
    }
    this.outPie = new Pie('.leave .at-theday-chart', outPieConfig)
  }

  /**
   * 渲染图表组件
   *
   * @param {array} data
   * @memberof RenderData
   */
  render() {
    const self = this
    // 来津离津人数
    fetch('fetchPopulationMovement', (data) => {
      $('.day-in-count').html(data.count.dayInCount)
      $('.day-out-count').html(data.count.dayOutCount)
      $('.year-in-count').html(data.count.yearInCount)
      $('.year-out-count').html(data.count.yearOutCount)
    })
    // 请求按高危省份展示来津和离津数据
    fetch('fetchHighrisk', (data) => {
      self.inPie.render(data.inTianjin)
      self.outPie.render(data.outTianjin)
    })
  }

  init() {
    this.render()
  }
}



