import mockApis from './mock'
import loader from '@/loader/loader'
import RenderMap from './scripts/map'
import RenderCount from './scripts/count'
import RederPopulationMovement from './scripts/populationMovement'
import RederWeiwenArea from './scripts/weiwenArea'
import RederWeiwenGroup from './scripts/weiwenGroup'
import './index.css'

const renderCount = new RenderCount()
const renderPopulationMovement = new RederPopulationMovement()
const renderWeiwenArea = new RederWeiwenArea()
const renderWeiwenGroup = new RederWeiwenGroup()
const renderMap = new RenderMap()

loader.load({
    apis: mockApis,
    config: {},
    init() {
        renderMap.render()
        renderCount.render()
        renderPopulationMovement.render()
        renderWeiwenArea.render()
        renderWeiwenGroup.render()
    }
})


