// import $ from 'jquery'
import { fetch } from '@/util/request'

import {
    // ChinaMap,
    Map
} from '@/charts/index'

export default class RenderMap {
    constructor() {
        // this.chinaMap = new ChinaMap('.map')
        this.chinaMap = new Map('map')
    }

    /**
     * 渲染图表组件
     * 
     * @param {array} data 
     * @memberof RenderData
     */
    render() {
        const self = this
        fetch('fetchMap',
            (data) => {
                self.chinaMap.render(data.map)
            })
    }
}


